.arrow {
	position: absolute;
	border-left: 30px solid transparent;
	border-right: 30px solid transparent;
	border-top: 30px dotted rgb(255, 255, 255);
	bottom: 5%;
	-webkit-animation: arrowtaunt infinite 1s linear;
	        animation: arrowtaunt infinite 1s linear;
}

@-webkit-keyframes arrowtaunt {
	0% {
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
	}
	50% {
		-webkit-transform: translateY(20px);
		        transform: translateY(20px);
	}
	100% {
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
	}
}

@keyframes arrowtaunt {
	0% {
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
	}
	50% {
		-webkit-transform: translateY(20px);
		        transform: translateY(20px);
	}
	100% {
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
	}
}

